import React from 'react';
import PropTypes from 'prop-types';
import { graphql, Link } from 'gatsby';
import Img from 'gatsby-image';

import Layout from 'components/Layout';
import SEO from 'components/SEO';
import Card from 'components/Card';
import Title from 'components/Title';

export const queryImage = graphql`
  query {
    propject1: file(relativePath: { eq: "flappy.jpg" }) {
      childImageSharp {
        fixed(width: 96, height: 96) {
          ...GatsbyImageSharpFixed
        }
      }
    }
    project2: file(relativePath: { eq: "stopwatch_46861.png" }) {
      childImageSharp {
        fixed(width: 96, height: 96) {
          ...GatsbyImageSharpFixed
        }
      }
    }
    project3: file(relativePath: { eq: "infusion1.jpg" }) {
      childImageSharp {
        fixed(width: 96, height: 96) {
          ...GatsbyImageSharpFixed
        }
      }
    }
    project4: file(relativePath: { eq: "slack-parrot.png" }) {
      childImageSharp {
        fixed(width: 96, height: 96) {
          ...GatsbyImageSharpFixed
        }
      }
    }
  }
`;

const Projects = ({ data }) => (
  <Layout>
    <SEO
      pageTitle="Projects"
      description="Front-end developer, developing apps and web pages"
      keywords="React, Node, Gatsby, Front-end, developer, API"
    />
    <section className="section is-size-4-desktop is-size-5-touch">
      <Title>Projects</Title>
      <p className="has-text-centered">
        See more on my
        {' '}
        <a href="https://github.com/thiagomedina">
          GitHub
        </a>
        .
      </p>
      <div className="columns is-centered">
        <div className="column is-half">
          <Card
            title="FlappyBird"
            subtitle="
            Game developed with javascript and CSS"
            link="https://thiagomedina.github.io/flappybird"
            tags={['Javascript', 'CSS']}
            image={<Img fixed={data.propject1.childImageSharp.fixed} alt="Gatsby Course" />}
          />
          <Card
            title="Timer"
            subtitle="
             Developed with JQuery"
            link="https://thiagomedina.github.io/conometro"
            tags={['JQuery']}
            image={<Img fixed={data.project2.childImageSharp.fixed} alt="Kassel Labs logo" />}
          />
          <Card
            title="Template Infusion"
            subtitle="Template developed with html, css, javascrip"
            link="https://thiagomedina.github.io/project/"
            tags={['HTML', 'CSS', 'Javascript']}
            image={<Img fixed={data.project3.childImageSharp.fixed} alt="Tic tac toe with porgs and Chewbacca" />}
          />
          {/* <Card
            title="Template Infusion"
            subtitle=""
            link="https://www.npmjs.com/package/slack-parrot"
            tags={['Node']}
            image={<Img fixed={data.project4.childImageSharp.fixed} alt="Party parrot animation" />}
          /> */}
        </div>
      </div>

    </section>
  </Layout>
);

Projects.propTypes = {
  data: PropTypes.object.isRequired,
};

export default Projects;
